<template>
    <div id="discussItem" :class="$style.header">
        <div :class="$style.div01">
            <img :src="imgPath" alt="">
            <span :class="$style.span01">{{coinType}}</span>
        </div>
        <div>{{articleCount}}</div>
        <div>{{viewCount}}</div>
        <div :class="$style.divTitle">
            <span v-on:click="clickTitle" :style="{cursor:'pointer'}">
                {{title}}
            </span>
            <span>
                {{formatDate(publishDate)}}
            </span>
        </div>
    </div>
</template>

<script>

const coinTypeAry = [
    {title: 'Bitcoin', iconFileName: 'bit.svg'},
    {title: 'Ethereum', iconFileName: 'eth.svg'},
    {title: 'IOTA', iconFileName: 'IOTA.svg'},
    {title: 'Grin', iconFileName: 'GRIN.svg'},

    {title: 'Bitcoin Cash', iconFileName: 'BCH.svg'},
    {title: 'Ripple', iconFileName: 'xrp.svg'},
    {title: 'Ether classic', iconFileName: 'ETC.svg'},
    {title: 'Monero', iconFileName: 'XMR.svg'},

    {title: 'Horizen', iconFileName: 'ZEN.svg'},
    {title: 'Zcash', iconFileName: 'ZEC.svg'},
    {title: 'USDT', iconFileName: 'USDT.svg'},
    {title: 'DAI', iconFileName: 'DAI.svg'},
    {title: 'GUSD', iconFileName: 'GUSD.svg'},
    {title: 'P3D', iconFileName: 'p3D.svg'},
]


export default {
    name: 'DisscussItem',
    components: {
    },
    props: {
        title: String,
        coinType: String,
        articleCount: Number,
        viewCount: Number,
        publishDate: Date,
    },
    methods: {
        formatDate: function (date) {
            var d = new Date(date),
                month = "" + (d.getMonth() + 1),
                day = "" + d.getDate(),
                year = d.getFullYear();

            if (month.length < 2) month = "0" + month;
            if (day.length < 2) day = "0" + day;

            return [year, month, day].join("-");
        },
        clickTitle() {
            this.$router.push('/read_article/998');
        }
    },
    computed: {
		imgPath: function() {
            const rstObj = coinTypeAry.find((item) => {
                return item.title === this.coinType;
            });
            if (rstObj) {
                return require('@/assets/icon/crypto/' + rstObj.iconFileName);
            } else {
                return '';
            }
			
		}
    },
    created() {
        console.log(this)
    }
};
</script>

