.header {
    height: 72px;
    background-color: white;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 3fr;
    color: #394A58;
    font-size: 16px;
    align-items: center;
    border-bottom: 1px solid #D7DDE3;
}

.header div {
    margin-left: 24px;
}

.div01 {
    display: flex;
    align-items: center;
}

.span01 {
    margin-left: 13px;
    min-width: 70px;
}
.divTitle {
    font-weight: 500;
}
.divTitle span {
    display: block;
}
.divTitle span:nth-child(1) {
    font-weight: 500;
}
.divTitle span:nth-child(2) {
    font-size: 14px;
    color: #9AABB8;
}

</style>