<script>
import { mapGetters, mapActions } from "vuex";

export default {
    name: "Login",
    props: {
        title: String,
        url: String
    },
    data() {
        return {
            inputPwdType: "password"
        };
    },
    methods: {
        ...mapActions(["setLoginStatus"]),
        eyePressed() {
            this.inputPwdType = "text";
        },
        eyeUp() {
            this.inputPwdType = "password";
        },
        loginClick() {
            this.setLoginStatus(true);
        }
    },
    created() {
        if (this.$cookies.get('login')) {
            console.log(this.$cookies.get('login'));
            this.$router.push('/')
        }
    },
    computed: mapGetters({
        loginStatus: "getLoginStatus",
    }),
    watch: {
        loginStatus(val) {
            if (val) {
                this.$router.push('/')
            }
        }
    }
};
</script>

