<template>
    <div :class="$style.mainDiv">
        <div id="ListArticle" :class="$style.div02">
            <div class="font01">系統 / 收藏文章</div>
            <div class="font02">收藏文章</div>



            <div :class="$style.div03">
                <select name id :class="$style.select01">
                    <option value="看板">看板</option>
                    <option value="看板">看板</option>
                    <option value="看板">看板</option>
                    <option value="看板">看板</option>
                    <option value="看板">看板</option>
                </select>

                <select name id :class="[$style.select01, $style.select02]">
                    <option value="討論串">討論串</option>
                    <option value="討論串">討論串</option>
                    <option value="討論串">討論串</option>
                    <option value="討論串">討論串</option>
                    <option value="討論串">討論串</option>
                </select>
                <div :class="$style.divSearchIcon">
                    <img src="~@/assets/icon/search-ondark.svg" alt />
                </div>
            </div>

            <div :class="$style.div04">
                <div :style="{display: 'flex'}">
                    <span
                        v-bind:class="[$style.span01, filterType1=== 1 ? $style.activeSpan01 : '']"
                        v-on:click="updateFilter1(1)"
                    >
                        最新發佈
                    </span>
                    <span
                        v-bind:class="[$style.span01, $style.marginLeft01, filterType1=== 2 ? $style.activeSpan01 : '']"
                        v-on:click="updateFilter1(2)"
                    >
                        最新加入
                    </span>
                </div>
            </div>

			<div :class="$style.div06">
				<label :class="checkbox.container">
					<input type="checkbox" v-model="allChecked" v-on:click="allCheckClick" />
					<span :class="checkbox.checkmark"></span>
				</label>

				<span :class="$style.span02">全選</span>

				<button class="btn01" :style="{marginLeft: '24px'}">
					<img src="~@/assets/icon/trash_black.svg" alt />
					刪除
				</button>
			</div>



            <div :class="$style.div05">
				<List />

            </div>

            <Paging />
        </div>
    </div>
</template>

<script>
import List from './List.vue'
import { EventBus } from './EventBus.js';
import Paging from '@/components/Common/Paging.vue';

export default {
    components: {
        List,
        Paging
    },
    data() {
        return {
            filterType1: 1,
			filterType2: 1,
			allChecked: false,
			selectedData: []
        };
    },
    methods: {
        updateFilter1: function(index) {
            this.filterType1 = index;
        },
        updateFilter2: function(index) {
            this.filterType2 = index;
		},
		itemCheck(itemObj) {
			const index = this.selectedData.indexOf(itemObj.id);
			if (itemObj.checked) {
				if (index === -1) {
					this.selectedData.push(itemObj.id);
				}
			} else {
				if (index !== -1) {
					this.selectedData.splice(index, 1);
				}
				this.allChecked = false;
			}
			console.log(this.selectedData);
		},
		allCheckClick(e) {
			EventBus.$emit('CHECK_ALL_EVENT', e.target.checked);
		}
	},
	created() {
		EventBus.$on('ITEM_CHECK_EVENT', this.itemCheck);
	},
	destroyed() {
		EventBus.$off('ITEM_CHECK_EVENT', this.itemCheck);
	}
};
</script>

