import VueCookies from 'vue-cookies'

const state = {
    isMorePanel: false,
    isAccountMenu: false,
    loginStatus: VueCookies.get('login'),
    selectMenuId: 0,
    isAuthorInfoPanel: false,
    isSearchPanelM: false,
}

const getters = {
    getIsMorePanel: state => state.isMorePanel,
    getLoginStatus: state => state.loginStatus,
    getSelectMenuId: state => state.selectMenuId,
    getIsAccountMenu: state => state.isAccountMenu,
    getIsAuthorInfoPanel: state => state.isAuthorInfoPanel,
    getIsSearchPanelM: state => state.isSearchPanelM,
}

const actions = {
    setIsMorePanel({ commit }, flag) {
        commit('setIsMorePanel', flag);
    },
    setLoginStatus({ commit }, flag) {
        commit('setLoginStatus', flag);
    },
    setSelectMenuId({ commit }, flag) {
        commit('setSelectMenuId', flag);
    },
    setIsAccountMenu({ commit }, flag) {
        commit('setIsAccountMenu', flag);
    },
    setIsAuthorInfoPanel({ commit }, flag) {
        commit('setIsAuthorInfoPanel', flag);
    },
    setIsSearchPanelM({ commit }, flag) {
        commit('setIsSearchPanelM', flag);
    },
}



const mutations = {
    setIsMorePanel(state, flag) {
        state.isMorePanel = flag;
    },
    setLoginStatus(state, flag) {
        state.loginStatus = flag;

        if (flag) {
            VueCookies.set('login','true');
        } else {
            VueCookies.remove('login');
        }
        
    },
    setSelectMenuId(state, id) {
        state.selectMenuId = id;
    },
    setIsAccountMenu(state, flag) {
        state.isAccountMenu = flag;
    },
    setIsAuthorInfoPanel(state, flag) {
        state.isAuthorInfoPanel = flag;
    },
    setIsSearchPanelM(state, flag) {
        state.isSearchPanelM = flag;
    }
}

export default {
    state,
    getters,
    actions,
    mutations
}