<template>
    <div :class="$style.divLogin">
        <div :class="$style.div01">首頁 / 會員註冊</div>
        <div :class="$style.div02">會員註冊</div>

        <div :class="$style.divProgress">
            <div :class="[$style.divCircle]" :style="getStyle(1)">1</div>
            <div :class="$style.divConnectLine" :style="getStyle(2)"></div>
            <div :class="$style.divCircle" :style="getStyle(2)">2</div>
            <div :class="$style.divConnectLine" :style="getStyle(3)"></div>
            <div :class="$style.divCircle" :style="getStyle(3)">3</div>
        </div>

        <div :class="$style.divProgress" :style="{marginTop: '8px'}">
            <span :class="$style.span04">基本資料</span>
            <span :class="[$style.span04,$style.margin01]">驗證資料</span>
            <span :class="$style.span04">完成註冊</span>
        </div>

        <div :class="$style.divProgressM">
            <div :class="$style.divCircleM">{{step}}/3</div>
            <span :class="$style.spanStepStr">{{getStepStr()}}</span>
        </div>

        <Step1 v-on:REGISTER_STEP="setStep($event)" v-if="step === 1" />
        <Step2 v-on:REGISTER_STEP="setStep($event)" v-if="step === 2" />
        <Step3 v-on:REGISTER_STEP="setStep($event)" v-if="step === 3" />
    </div>
</template>

<script>
import Step1 from "./Step1.vue";
import Step2 from "./Step2.vue";
import Step3 from "./Step3.vue";

const stepStrAry = ["基本資料", "驗證資料", "完成註冊"];

export default {
    name: "Register",
    components: {
        Step1,
        Step2,
        Step3
    },
    props: {
        title: String,
        url: String
    },
    data() {
        return {
            step: 1
        };
    },
    methods: {
        setStep(index) {
            console.log("REGISTER_STEP", index);
            this.step = index;
        },
        getStyle(index) {
            if (this.step >= index) return { backgroundColor: "#1DA3FB" };
        },
        getStepStr() {
            return stepStrAry[this.step - 1];
        }
    }
};
</script>

