<template>
    <div id="Item" :class="$style.header">
        <div :class="$style.div01" class="flex_a_center">
            <span class="spanTag" :class="$style.rwd_01">{{tag}}</span>

            <label :class="checkbox.container">
                <input type="checkbox" v-model="vCheck" @click="checkClick" />
                <span :class="checkbox.checkmark"></span>
            </label>
        </div>
        <div :class="$style.divTitle" v-on:click="clickTitle">{{title}}</div>

        <div :class="$style.divContent">{{content}}</div>

        <div :class="$style.divPublishDate">
			<span :class="$style.rwd_01">發布日期：</span>
			{{formatDate(publishDate)}}
		</div>
        <div :class="$style.divLastUpdateDate">{{formatDate(lastUpdateDate)}}</div>

        <div class="flex_a_center" :class="$style.divLike">
            <img :class="$style.img01" src="~@/assets/icon/like.svg" alt />
            {{likeCount}}
        </div>
        <div class="flex_a_center" :class="$style.divComment">
            <img :class="$style.img01" src="~@/assets/icon/comment.svg" alt />
            {{commentCount}}
        </div>
        <div class="flex_a_center" :class="$style.divSuggest">
            <img :class="$style.img01" src="~@/assets/icon/edit.svg" alt />
            {{suggestCount}}
        </div>
    </div>
</template>

<script>
import { EventBus } from "./EventBus.js";

export default {
    name: "Item",
    data() {
        return {
            vCheck: false
        };
    },
    props: {
        id: Number,
        title: String,
        tag: String,
        content: String,
        publishDate: Date,
        lastUpdateDate: Date,
        likeCount: Number,
        commentCount: Number,
        suggestCount: Number
    },
    methods: {
        formatDate: function(date) {
            var d = new Date(date),
                month = "" + (d.getMonth() + 1),
                day = "" + d.getDate(),
                year = d.getFullYear();

            if (month.length < 2) month = "0" + month;
            if (day.length < 2) day = "0" + day;

            return [year, month, day].join("-");
        },
        checkClick(e) {
            EventBus.$emit("ITEM_CHECK_EVENT", {
                checked: e.target.checked,
                id: this.id
            });
        },
        checkAllEvent(flag) {
            this.vCheck = flag;
            EventBus.$emit("ITEM_CHECK_EVENT", { checked: flag, id: this.id });
        },
        clickTitle() {
            this.$router.push('/read_article/998');
        }
    },
    created() {
        EventBus.$on("CHECK_ALL_EVENT", this.checkAllEvent);
    },
    destroyed() {
        EventBus.$off("CHECK_ALL_EVENT", this.checkAllEvent);
    }
};
</script>

