<template>
    <div class="divHotArticleItem">
        <span class="aItem" v-on:click="clickFunc">{{title}}</span>
        <div class="divContent">{{content}}</div>
        <div class="divBottom">
            <span class="spanDate">日期：{{formatDate(tDate)}}</span>
            <div class="divSepV"></div>
            <span class="spanDate">收件者：{{receiver}}</span>

			<div class="divDelete">
                <img src="~@/assets/icon/trash.svg" alt />
                <span class="spanDelete">
                    刪除
                </span>
            </div>

			<div class="divDelete" :style="{marginLeft: '24px'}">
                <img src="~@/assets/icon/reply.svg" alt />
                <span class="spanReply" v-on:click="replyClick">
                    回覆
                </span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "MessageItem",
    props: {
        title: String,
        content: String,
		tDate: Date,
		receiver: String,
		url: String,
    },
    methods: {
        formatDate: function(date) {
            var d = new Date(date),
                month = "" + (d.getMonth() + 1),
                day = "" + d.getDate(),
                year = d.getFullYear();

            if (month.length < 2) month = "0" + month;
            if (day.length < 2) day = "0" + day;

            return [year, month, day].join("-");
		},
		clickFunc() {
			this.$router.push('/read_message');
        },
        replyClick() {
            this.$router.push('/reply_message');
        }
    }
};
</script>

