        coinType: "Ethereum",
        articleCount: 100,
        viewCount: 29302,
        publishDate: new Date('2019/08/01')
    },

    {
        title: "區塊鏈生產履歷有多神？從小農到生技業都搶著用",
        coinType: "Ripple",
        articleCount: 100,
        viewCount: 29302,
        publishDate: new Date('2019/08/01')
    },

    {
        title: "區塊鏈生產履歷有多神？從小農到生技業都搶著用",
        coinType: "Ether classic",
        articleCount: 100,
        viewCount: 29302,
        publishDate: new Date('2019/08/01')
    },

    {
        title: "區塊鏈生產履歷有多神？從小農到生技業都搶著用",
        coinType: "Monero",
        articleCount: 100,
        viewCount: 29302,
        publishDate: new Date('2019/08/01')
    }
];

export default {
    name: "discussList",
    components: {
        DisscussItem,
        DisscussItemM
    },
    data() {
        return {
            window: {
                width: 0,
                height: 0
            },
            dynamicComponent: "DisscussItem",
            dataAry: dataAry
        };
    },
    created() {
        window.addEventListener("resize", this.handleResize);
        this.handleResize();
    },
    destroyed() {
        window.removeEventListener("resize", this.handleResize);
    },
    methods: {
        handleResize() {
            this.window.width = window.innerWidth;
            this.window.height = window.innerHeight;
            console.log(
                "Width:",
                this.window.width,
                " Height:",
                this.window.height
            );
            if (window.innerWidth > 800) {
                this.dynamicComponent = "DisscussItem";
            } else {
                this.dynamicComponent = "DisscussItemM";
            }
        },
        currentProperties: function(item) {
            if (this.dynamicComponent === "DisscussItem") {
                console.log('DisscussItem',item.title);
                return item
            } else if (this.dynamicComponent === "DisscussItemM") {
                return {
                    title: item.title,
                    content: item.title,
                    url: '',
                    tag: item.coinType,
                    //likeCount: Number,
                    //suggestCount: Number,
                    //commentCount: Number,
                    publishDate: item.publishDate,
                    //lastModifyDate: Date
                }
            }
        }
    },
    computed: {
        
    }
};
