<template>
    <div :class="$style.headerDiv">
        <div :class="$style.divLogo">
            <img :class="$style.logoImg" alt="logo" src="~@/assets/logo/mark.svg" />
            <span :class="$style.title01">Blockchain forum</span>

            <div :class="$style.searchInputDiv">
                <input :class="$style.input01" type="text" placeholder="搜尋" />
                <router-link :class="$style.searchIcon" to="/site_search">
                    <img alt src="~@/assets/icon/search-ondark.svg" />
                </router-link>
            </div>
        </div>

        <div :class="[$style.userInfoDiv,$style.rwd_disNone]" v-if="loginStatus">
            <img alt src="~@/assets/icon/user.svg" />
            <span :class="$style.spanUserName">林雅婷</span>
            <img
                :style="{cursor: 'pointer'}"
                alt
                src="~@/assets/icon/chevron-down-ondark.svg"
                v-on:click="setIsAccountMenu(true)"
                ref="imgDown"
            />
        </div>

        <div :class="[$style.userInfoDiv, $style.userInfoDivM]" v-if="loginStatus">
            <img
                :class="$style.imgSearchM"
                alt
                src="~@/assets/icon/search-ondark.svg"
                v-on:click="setIsSearchPanelM(true)"
            />
            <div :class="$style.divSepV"></div>
            <img
                :style="{cursor: 'pointer'}"
                alt
                src="~@/assets/icon/user.svg"
                v-on:click="setIsAccountMenu(true)"
                ref="imgDownM"
            />
        </div>

        <div :class="$style.userInfoDiv" v-if="!loginStatus">
            <img alt src="~@/assets/icon/user.svg" />
            <router-link
                :class="$style.span01"
                to="/login"
                v-on:click.native="setSelectMenuId(0)"
            >登入/註冊</router-link>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
    name: "Header",
    props: {
        msg: String
    },
    computed: mapGetters({
        loginStatus: "getLoginStatus",
        isAccountMenu: "getIsAccountMenu"
    }),
    methods: {
        ...mapActions([
            "setSelectMenuId",
            "setIsAccountMenu",
            "setIsSearchPanelM"
        ]),
        docClick: function(e) {
            if (this.$refs.imgDown && this.$refs.imgDownM) {
                if (
                    !this.$refs.imgDown.contains(e.target) &&
                    !this.$refs.imgDownM.contains(e.target) &&
                    this.loginStatus
                ) {
                    if (this.isAccountMenu) {
                        this.setIsAccountMenu(false);
                    }
                }
            }
        }
    },
    created() {
        document.addEventListener("click", this.docClick);
    },
    destroyed() {
        document.removeEventListener("click", this.docClick);
    }
};
</script>

