<script>
import ReplyArticleItem from "./ReplyArticleItem.vue";
import SuggestArticleItem from "./SuggestArticleItem.vue";

import { mapActions } from "vuex";

export default {
    name: "ReadArticle",
    components: {
        ReplyArticleItem,
        SuggestArticleItem
    },
    props: {
        board: String,
        discussTitle: String,
        topicTitle: String,
        originalContent: String
    },
    data() {
        return {
            filterType1: 1,
            filterType2: 1,
            filterType3: 2,
            refFlag: false
        };
    },
    methods: {
        ...mapActions(["setIsAuthorInfoPanel"]),
        buttonClick() {},
        replyButtonClick() {
            this.$router.push("/reply_article");
        },
        formatDate: function(date) {
            var d = new Date(date),
                month = "" + (d.getMonth() + 1),
                day = "" + d.getDate(),
                year = d.getFullYear();

            if (month.length < 2) month = "0" + month;
            if (day.length < 2) day = "0" + day;

            return [year, month, day].join("-");
        },
        updateFilter1: function(index) {
            this.filterType1 = index;
        },
        updateFilter2: function(index) {
            this.filterType2 = index;
        },
        updateFilter3: function(index) {
            this.filterType3 = index;
        },
        refClick() {
            this.refFlag = !this.refFlag;
        }
    },
    computed: {
        refImgPath: function() {
            let iconName = this.refFlag ? "up.svg" : "down.svg";
            return require("@/assets/icon/" + iconName);
        }
    },
    created() {}
};
</script>

