<template>
    <div id="MenuHeader" :class="$style.headerDiv">
        <span
            v-bind:style="[item.id === selectMenuId ? {borderBottom: 'solid 4px #FFF',padding: '10px 0 6px'} : {}]"
            v-for="item in menu01"
            v-bind:key="item.id"
            v-on:click="updateMenu(item.id)"
        >
            <router-link :class="$style.spanMenu" v-bind:to="item.path">{{ item.name }}</router-link>
        </span>

        <span ref="SpanMore" v-on:click="openMorePanel" :class="$style.spanMenu">
            密碼貨幣
        </span>

        <span
            v-bind:style="[item.id === selectMenuId ? {borderBottom: 'solid 4px #FFF',padding: '10px 0 6px'} : {}]"
            v-for="item in menu02"
            v-bind:key="item.id"
            v-on:click="updateMenu(item.id)"
        >
            <router-link :class="$style.spanMenu" v-bind:to="item.path">{{ item.name }}</router-link>
        </span>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";



const menuAry01 = [
    { id: 0, name: "首頁", path: "/" },
    { id: 9, name: "我的最愛", path: "/favorite" },
];

const menuAry02 = [
    { id: 2, name: "區塊鏈", path: "/cryptocurrency" },
    { id: 3, name: "分散式儲存", path: "/modify_suggest" },
    { id: 4, name: "經濟分析", path: "/" },
    { id: 5, name: "法律監管", path: "/" },
    { id: 6, name: "商業模式", path: "/" },
    { id: 7, name: "密碼學", path: "/" },
    { id: 8, name: "時事", path: "/" },
];

export default {
    name: "MenuHeader",
    components: {
    },
    props: {
        msg: {
            type: String,
            default: "djfkaljgiaopejfnadfak;"
        }
    },
    data() {
        return {
            menu01: menuAry01,
            menu02: menuAry02,
        };
    },
    methods: {
        ...mapActions([
            "setIsMorePanel",
            "setSelectMenuId"
        ]),
        updateMenu: function(index) {
            this.setSelectMenuId(index);
        },
        docClick: function(e) {
            //console.log("docClick",this.$refs)
            if (!this.$refs.SpanMore.contains(e.target) && this.isMorePanel)
                this.setIsMorePanel(false);
        },
        openMorePanel: function() {
            if (!this.isMorePanel)
                this.setIsMorePanel(true);
        }
    },
    computed: mapGetters({
        isMorePanel: "getIsMorePanel",
        selectMenuId: "getSelectMenuId"
    }),
    created() {
        document.addEventListener("click", this.docClick);
    },
    destroyed() {
        document.removeEventListener("click", this.docClick);
    }
};
</script>

<style module>
.headerDiv {
    width: 100%;
    height: 56px;
    position: relative;
    background: linear-gradient(
        89.97deg,
        #1da3fb 0%,
        #528cff 20%,
        #45d6a6 80%,
        #18cc54 100%
    );
    padding: 0 120px;
    display: grid;
    grid-template-columns: auto;
    grid-auto-flow: column;
    grid-gap: 15px;
    align-items: center;
    justify-items: center;
    justify-content: space-between;
}

.spanMenu {
    color: rgba(255, 255, 255, 0.8);
    font-size: 16px;
    cursor: pointer;
    text-decoration: none;
    white-space: nowrap;
}

@media only screen and (max-width: 1000px) {
    .headerDiv {
        padding: 0 20px;
        overflow-x: auto;
    }
    .headerDiv::after {
        content: "";
        display: block;
        width: 10px;
        height: 1px;
    }
}
</style>