<template>
    <div class="divHotArticleItem">
        <div>
            <span class="spanTag">{{tag}}</span>
        </div>
        <a v-on:click="clickTitle" class="aItem">{{title}}</a>
        <div class="divContent">{{content}}</div>
        <div class="divBottom">
            <span class="spanDate">發布日期：{{formatDate(publishDate)}}</span>
            <div class="divSepV"></div>
            <span class="spanDate rwd_none">最後更新：{{formatDate(lastModifyDate)}}</span>
            <div class="divLike">
                <img src="~@/assets/icon/like.svg" alt />
                <span class="spanLike">
                    <span class="rwd_none">讚</span>
                    {{likeCount}}
                </span>
            </div>
            <div class="divSuggest">
                <img src="~@/assets/icon/edit.svg" alt />
                <span class="spanLike">
                    <span class="rwd_none">修改建議</span>
                    {{suggestCount}}
                </span>
            </div>
            <div class="divSuggest">
                <img src="~@/assets/icon/edit.svg" alt />
                <span class="spanLike">
                    <span class="rwd_none">評論</span>
                    {{commentCount}}
                </span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "ArticleItem",
    props: {
        title: String,
        content: String,
        url: String,
        tag: String,
        likeCount: Number,
        suggestCount: Number,
        commentCount: Number,
        publishDate: Date,
        lastModifyDate: Date
    },
    methods: {
        formatDate: function(date) {
            var d = new Date(date),
                month = "" + (d.getMonth() + 1),
                day = "" + d.getDate(),
                year = d.getFullYear();

            if (month.length < 2) month = "0" + month;
            if (day.length < 2) day = "0" + day;

            return [year, month, day].join("-");
        },
        clickTitle() {
            this.$router.push('/read_article/998');
        }
    }
};
</script>

