<script>
import { mapGetters, mapActions } from "vuex";

/*const linkArray = [
    { title: "個人設定", to: "" },
    { title: "我的文章", to: "" },
    { title: "好友名單", to: "" },
    { title: "私人訊息", to: "" },

    { title: "收藏文章", to: "" },
    { title: "我的最愛", to: "" },
    { title: "帳號登出", to: "", clickFunc: this.setLoginStatus(false) }
];*/

export default {
    name: "AccountMenu",
    components: {},
    data() {
        return {
            linkArray: [
                { title: "個人設定", clickFunc: () => this.fPush("/setting") },
                {
                    title: "我的文章",
                    clickFunc: () => this.fPush("/myarticle")
                },
                { title: "好友名單", clickFunc: () => this.fPush("/friend") },
                { title: "私人訊息", clickFunc: () => this.fPush("/message") },
                {
                    title: "收藏文章",
                    clickFunc: () => this.fPush("/article_collect")
                },
                { title: "我的最愛", clickFunc: () => this.fPush("/favorite") },
                {
                    title: "帳號登出",
                    to: "",
                    clickFunc: () => {
                        this.setLoginStatus(false);
                        this.setIsAccountMenu(false);
                    }
                }
            ]
        };
    },
    computed: mapGetters({
        isAccountMenu: "getIsAccountMenu"
    }),
    methods: {
        ...mapActions(["setLoginStatus", "setIsAccountMenu"]),
        fPush(path) {
            this.$router.push({ path });
        }
    }
};
</script>

